<template>
    <a-spin :spinning="loading">
      <a-form-model
        ref="form"
        :model="entity"
        :rules="rules"
        autocomplete="off"
        layout="vertical"
      >
        <div class="card">
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item label="培训名称" prop="trainingName">
                <a-input
                  v-model="entity.trainingName"
                  placeholder="请输入培训名称"
                  name="trainingName"
                />
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="受训人" prop="trainee">
                <a-input
                  v-model="entity.trainee"
                  placeholder="请输入受训人"
                  name="trainee"
                />
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="培训状态" prop="status">
                <a-select
                  v-model="entity.status"
                  placeholder="请选择培训状态"
                  name="status"
                >
                  <a-select-option value="进行中">进行中</a-select-option>
                  <a-select-option value="已完成">已完成</a-select-option>
                  <a-select-option value="已取消">已取消</a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>
            <a-col :sm="12">
              <a-form-model-item label="描述" prop="description">
                <a-textarea
                  v-model:value="entity.description"
                  placeholder="请输入描述"
                  :rows="4"
                  :max-length="1000"
                  name="description"
                />
              </a-form-model-item>
            </a-col>
          </a-row>
        </div>
        <g-collapse headerName="其他信息">
          <template #content>
            <a-row :gutter="24">
              <a-col :sm="6">
                <a-form-model-item label="创建人">
                  <span>admin</span>
                </a-form-model-item>
              </a-col>
              <a-col :sm="6">
                <a-form-model-item label="修改时间">
                  <span> 2025-04-20 15:00</span>
                </a-form-model-item>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </a-form-model>
    </a-spin>
  </template>
  
  <script>
  import { GNdoPageEditBase } from "@/utils";
  export default {
    name: "CreateOrEditTrainingRecordStatus",
    mixins: [GNdoPageEditBase],
    data() {
      return {
        entity: {
          trainingName: "",
          trainee: "",
          status: "",
          description: "",
        },
        rules: {
          trainingName: [
            { required: true, message: "请输入培训名称", trigger: "change" },
          ],
          trainee: [
            { required: true, message: "请输入受训人", trigger: "change" },
          ],
          status: [
            { required: true, message: "请选择培训状态", trigger: "change" },
          ],
        },
      };
    },
    methods: {
      init() {
        this.loading = true;
        if (this.id) {
          // 模拟请求
          setTimeout(() => {
            this.loading = false;
            this.entity = {
              trainingName: "示例培训",
              trainee: "张三",
              status: "进行中",
              description: "这是一个示例培训的描述",
            };
          }, 500);
        } else {
          this.reset();
          this.loading = false;
        }
      },
      resetFormData() {
        this.entity = {
          trainingName: "",
          trainee: "",
          status: "",
          description: "",
        };
      },
      async save() {
        let res = await this.checkError();
        if (res) {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.saveSuccess(this.entity.trainingName);
            this.handleSaveEndBackView(this.id);
            this.refreshParent({
              reset: null,
            });
          }, 500);
        }
      },
    },
  };
  </script>
  
  <style lang="scss" scoped>
  </style>